html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  /* font-size: 18px !important; */
  font-size: 1em;
  border: none;
  margin: 0.5em;
  padding: 0.5em 1em 0.5em 1em;
  border: 1px solid black;
  width: auto;
  overflow: visible;
  cursor: pointer;
  background: white;
  outline: none;
  border-radius: 0.1em;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;

  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;

  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
}

.buttonGroup > button {
  margin: 0.5em 0 0.5em 0;
  padding: 0.5em 1em 0.5em 1em;
}

.buttonGroup > button:first-child {
  -webkit-transform: translateX(2px);
          transform: translateX(2px);
}

.buttonGroup > button:last-child {
  -webkit-transform: translateX(-2px);
          transform: translateX(-2px);
}

